<template>
  <Navbar @searchValue="search = $event" @header="header = $event"/>
  <Notes :notes="filterNotes" @delNote="delNote" @changeNote="changeNote" :header="header"/>
  <Modal 
  v-show="modalOpen" 
  @closeModal="closeModal" 
  @addNote="addNote"
  :edit="edit"
  :editNote="editNote"
  @editedNote="editedNote"
  />
  <AddButton @openModal="openModal"/>
</template>

<script>
import Navbar from '@/components/Navbar.vue'
import Notes from '@/components/Notes.vue'
import Modal from '@/components/Modal.vue'
import AddButton from '@/components/AddButton.vue'

export default {
  components: {
    Navbar,
    Notes,
    Modal,         
    AddButton,         
  },
  data(){
    return {
      header: true,
      search: null,
      edit: false,
      editNote: {},
      modalOpen: false, 
      notes: []
    }
  },
  computed: {
    filterNotes(){
      return this.search ? this.notes.filter(note => note.title.toLowerCase().includes(this.search.toLowerCase())) : this.notes;
    }
  },
  watch: {
    notes: {
      handler(newVal){
        localStorage.notes = JSON.stringify(this.notes)
      },
      deep: true,
    }
  },
  created(){
    this.getNotes()
  },
  methods: {
    editedNote(newNote){
      this.notes.forEach(note =>{
        if(note.id == newNote.id){
          note.title = newNote.title
          note.desc = newNote.desc
          note.date = newNote.date
        }
      })
    },
    changeNote(id){
      this.modalOpen = this.edit = true;
      this.editNote = this.notes.find(note => note.id == id)
    },
    getNotes(){
      let localNotes = localStorage.notes
      if(localNotes){
        this.notes = JSON.parse(localNotes)
      }
    },
    openModal(){
      this.modalOpen = true
    },
    closeModal(status){
      this.modalOpen = status
      setTimeout(() => {
        this.edit = false
      }, 500);
      
    },
    addNote(item){
      this.notes.push(item);
      this.modalOpen = false
    },
    delNote(id){
      let index = this.notes.findIndex( note => note.id == id)
      this.notes.splice(index, 1)
    }
  }
}
</script>

